  margin-left: -144px;
}

.btn {
  -o-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all 0.3s;

  cursor: pointer;
}

.bt {
  position: absolute;
  height: 287px;
  width: 287px;
  left: 50%;
  margin-left: -144px;
}
.btn-help {
  font-size: 35px;
  color: #fff;
  text-transform: uppercase;
  background: url(/help-button/static/media/btn.4d8a5e90.png) no-repeat center;
  height: 287px;
  width: 287px;
  line-height: 287px;
  position: absolute;
  z-index: 32px;
  margin-top: 7px;
}
.btn-help:active {
  margin-top: 14px;
}
.bt-fon {
  height: 287px;
  width: 287px;
  position: absolute;
  z-index: 1px;
  background: url(data:image/png;base64,) no-repeat center;
  margin-top: 7px;
}

input {
  height: 74px;
  line-height: 74px;
  margin-bottom: 15px;
  border: none;
  width: 80%;
  padding: 0 10%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.14);
  font-size: 19px;
  display: block;
  font-family: MUSEOSANSCYRL_300;
  border: 1px solid #fff;
}
textarea {
  max-width: 440px;
  height: 118px;
  resize: none;
  border: none;
  width: 80%;
  padding: 30px 10%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.14);
  font-size: 19px;
  display: block;
  font-family: MUSEOSANSCYRL_300;
  border: 1px solid #fff;
  margin-bottom: 15px;
}
.bt2 {
  height: 98px;
  position: relative;
  display: block;
  padding: 0 1px;
}
.bt-fon2 {
  background-color: #b31212;
  width: 100%;
  height: 91px;
  margin-top: 7px;
  position: absolute;
}
.btn-popup {
  background-color: #dd1e1e;
  width: 100%;
  height: 91px;
  position: absolute;
  line-height: 98px;
  color: #fff;
  font-size: 34px;
  font-family: MUSEOSANSCYRL_700;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.64);
}
.btn-popup:active {
  margin-top: 7px;
}

.ugol5 {
  border-radius: 5px;
