    background: none;
    position: relative;
    margin-left: 0;
    left: 0;
  }
  .block {
    position: relative;
    left: 0;
    margin: 0 auto;
  }
  .company {
    position: relative;
    width: auto;
    margin-left: 0px;
    left: 0%;
    top: 0%;
