/** +++++++++++++++++++++++++++ POPUP +++++++++++++++++++++++++++ **/

.scroll {
  display: none;
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow-y: scroll;
  z-index: 999;
}

#hover {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #000;
  opacity: 0.6;
  filter: alpha(opacity=60);
  cursor: pointer;
}
#hover img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.popup-spasibo {
  display: none;
  max-width: 590px;
  width: 85%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0px auto;
  text-align: center;
  color: #2f2f2f;
}

.popup-spasibo img {
  margin: 60px 0 30px 0;
}
.popup-spasibo .name {
  font-size: 34px;
  font-family: MUSEOSANSCYRL_900;
  text-transform: uppercase;
}
.popup-spasibo .desc {
  font-size: 23px;
  margin: 20px 0 60px 0;
  text-transform: uppercase;
}

.popup-support {
  display: none;
  max-width: 590px;
  width: 85%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0px auto;
  text-align: center;
  color: #2f2f2f;
}
.popup-support .desc {
  font-size: 18px;
  margin: 30px 20px;
}

